import React, { Component } from 'react';
import CornCodes from './imgs/CornCodes.png';
import CornDesign from './imgs/CornDesign.jpg';
import CornPower from './imgs/CornPower.jpeg';
import Card from '../Components/Card';
import Row from 'react-bootstrap/Row';
import Container from 'react-bootstrap/Container';


class Carousel extends Component {

    constructor(props) {
        super(props);
        this.state = {
            items: [
                {
                    id: 0,
                    title: 'Corn Design',
                    subTitle: 'Graphics Design',
                    imgSrc: CornDesign,
                    link: 'https://corndesign.wordpress.com',
                    selected: false
                },

                
                    {
                        id: 1,
                        title: 'Corn Codes',
                        subTitle: 'Web Development',
                        imgSrc: CornCodes,
                        link: 'https://corndesign.wordpress.com',
                        selected: false
                    },

                    
                        {
                            id: 2,
                            title: 'Corn Power',
                            subTitle: 'Production of PowerBanks',
                            imgSrc: CornPower,
                            link: 'https://corndesign.wordpress.com',
                            selected: false
                        },
            ]

        }
    }

    handleCardClick = (id, card) =>  {
        let items=[...this.state.items];

        items[id].selected = items[id].selected ? false : true;

        items.forEach( item =>{
            if(item.id!== id) {
                item.selected =false;
            }
        }

        );

        this.setState({
            items
        });
 
    }


    makeItems =  (items) => {
        return items.map(item  =>{
            return <Card item={item} click={(e => this.handleCardClick(item.id , e))} key={items.id}/>
        })
    }

    render() {
        return (
            <Container fluid={true}>
                <Row className="justify-content-around">
                    {this.makeItems(this.state.items)}
                </Row>
            </Container>
        )
    }
}
 
export default Carousel
