import React, { Component } from 'react'
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
import Container from 'react-bootstrap/Container';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';

//LOGO
import Logo from "./components/Portifolio/img/weblogo.png"

import './App.css'
import Footer from './components/Portifolio/Components/Footer';
import HomePage from './components/Portifolio/Pages/HomePage'
import BlogPage from './components/Portifolio/Pages/BlogPage'
import PortfolioPage from './components/Portifolio/Pages/PortfolioPage'
import ServicesPage from './components/Portifolio/Pages/ServicesPage'
import AboutPage from './components/Portifolio/Pages/AboutPage'
import ContactPage from './components/Portifolio/Pages/ContactPage'


class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      title: 'Corn Concept',
      headerLinks: [
        {title: 'Home' , path:'/'},
        {title: 'Blog' , path:'/Blog'},
        {title: 'Portfolio' , path:'/Portfolio'},
        {title: 'Services' , path:'/Services'},
        {title: 'About' , path:'/about'},
        {title: 'Contact' , path:'/contact'},
      ],
      home:{
        title: 'Corn Concept',
        subTitle: 'Corn group of Company',
        text: 'Checkout what we do'
      },

      blog:{
        title: 'Blog'
      },

      portfolio:{
        title: 'Portfolio'
      },

      services:{
        title: 'Services'
      },

     about:{
        title: 'About Us',
      },

     contact:{
        title: 'Let\'s talk',
      }


    }
  }
  render() {
    return (
      <Router>
        <Container  className="p-0" fluid={true}>
          <Navbar className="border-bottom" bg="transparent" expand="lg">
            <Navbar.Brand>
            <img src={Logo}/>
            </Navbar.Brand>

            <Navbar.Toggle className="border-0" aria-controls="navbar-toggle"/>
            <Navbar.Collapse id="navbar-toogle">

              <Nav className="ml-auto">
                <Link className="nav-link" to="/">Home</Link>
                <Link className="nav-link" to="/blog">Blog</Link>
                <Link className="nav-link" to="/portfolio">Portfolio</Link>
                <Link className="nav-link" to="/services">Services</Link>
                <Link className="nav-link" to="/about">About</Link>
                <Link className="nav-link" to="/contact">Contact</Link>
              </Nav>
            </Navbar.Collapse>
          </Navbar>

          <Route path="/" exact render={() => <HomePage title={this.state.home.title} subTitle={this.state.home.subTitle} text={this.state.home.text} />}/>
          <Route path="/blog" render={() => <BlogPage title={this.state.blog.title} />}/>
          <Route path="/portfolio" render={() => <PortfolioPage title={this.state.portfolio.title} />}/>
          <Route path="/services" render={() => <ServicesPage title={this.state.services.title} />}/>
          <Route path="/about" render={() => <AboutPage title={this.state.about.title}  />}/>
          <Route path="/contact" render={() => <ContactPage title={this.state.contact.title} />}/>
          <Footer/>

        </Container>
      </Router>
    )
  }
}

export default App
